import React from 'react';

import pic1 from '../../images/pic01.jpg';
import pic2 from '../../images/pic02.jpg';
import pic3 from '../../images/pic03.jpg';

import Feature from './Feature';

const Features = () => {
  const FEATURES = [
    {
      href: '/#',
      image: pic1,
      heading: 'Protects You and the People Around You',
      description: 'Contact tracing is the tracking of one’s interactions with the people around them during a pandemic. Using your own data, Contact Tracer helps you to determine if you are at risk and need to take action. In the event that you are infected, Contact Tracer can help you to notify others who you may have put at risk.',
      to: {
        href: 'https://en.wikipedia.org/wiki/Contact_tracing',
        label: 'Learn More About Contact Tracing',
      },
    },
    {
      href: '/#',
      image: pic2,
      heading: 'Private',
      description: 'All of you data is stored on your device, not the cloud. It is not shared with any third parties, or even us, the developers. You have the ability to choose to share this information with health professionals or anyone else, but it is completely your decision.',
      to: {
        href: 'https://github.com/dylantompkins/ContactTracer',
        label: 'Our Code',
      },
    },
    {
      href: '/#',
      image: pic3,
      heading: 'Open-Source',
      description: 'Open-source means that not only is our app free to use, but that developers all around the world have the opportunity to help us make the app better, safer, and even more useful. These developers help with our permission, and have no access to your data either.',
      to: {
        href: 'https://dylantompkins.github.io/ContactTracer/',
        label: 'More About Our Code',
      },
    },
  ];

  const [features] = React.useState(FEATURES);

  return (
    <section id="one" className="wrapper style2 spotlights">
       {features.map(feature => <Feature key={feature.heading} {...feature} />)}
    </section>
  );
}

export default Features;
