import React from 'react';

import Capability from './Capability';

const CapabilityList = () => {
  const CAPABILITIES = [
    {
      heading: 'Github',
      description: 'Enables our code to be public and open source so we can develop in an organized and efficient way.',
      iconClass: 'fa-code',
    },
    {
      heading: 'Flutter and Dart',
      description: 'Allows us to quickly implement useful features without having to deal with the obscure details of building an app.',
      iconClass: 'fa-lock',
    },
    {
      heading: 'Google Maps',
      description: 'Well-known platform to acquire and display location data. The API makes it very easy to display user data in a meaningful way.',
      iconClass: 'fa-cog',
    },
    {
      heading: 'Firebase',
      description: 'Helps us to manage the tools used to build the app in a secure way through remote configuration for API keys.',
      iconClass: 'fa-desktop',
    },
    {
      heading: 'SQLite',
      description: 'Database protocol used to organize and store user data locally on their device.',
      iconClass: 'fa-chain',
    },
    {
      heading: 'Open Covid-19 Data',
      description: 'This is a community updated database which we plan to integrate into our app to allow users to compare their personal location data to public data.',
      iconClass: 'fa-diamond',
    },
  ]

  const [capabilities] = React.useState(CAPABILITIES);

  return (
    <>
      {capabilities.map((capability) => <Capability key={capability.heading} {...capability} />)}
    </>
  )
}

export default CapabilityList;
